//리덕스 REDUX
import { createStore,combineReducers } from 'redux';

let rct_li = [];
function reducer3(state=rct_li,action){
    if(action.type='add-vlist'){
        const datacontain = action.payload;
        if(datacontain===null || datacontain===undefined){
            return state;

        }

        let json_li = localStorage.getItem('vlist');
        let return_jsondata =  JSON.parse(json_li);
        // let li = Object.values(String(return_jsondata));
        let li = return_jsondata===null;
        if(li){
            let copy = [...rct_li];                            
            copy.push(datacontain.title);
            localStorage.setItem('vlist',JSON.stringify(copy));
            return copy;
        }
        else{
            let copy = [...return_jsondata];
            let comp = copy.filter((item)=>{ return item===datacontain.title});
            if(comp.length==0){                
                copy.push(datacontain.title);   
            }        
            localStorage.setItem('vlist',JSON.stringify(copy));
            return copy;
        }
    }
    else{
        return state;
    }
}



//광고팝업닫기
function reducer2(state=true,액션){
    if(액션.type==='banner_close'){
        state=false;
        return state;
    }
    else{
        return state;
    }
}


//장바구니 테이블 리스트
let 초기값 = [
    // {id:0, name:'멋진신발', quantity:2},
    // {id:1, name:'멋진신발2', quantity:3}
];
function reducer(state=초기값,액션){

    switch(액션.type){
        case '수량증가':{            
            const datacontain = 액션.payload;
            let copy = [...state];
            //let data = state.findIndex((a)=>{ return a.id===datacontain.id});
            let comp = copy.filter((item)=>{ return item.id===datacontain.id});
            if(comp.length>0){                
                comp[0].quantity++;
            }
            return copy;
        }            
        case '수량감소': {
            const datacontain = 액션.payload;
            let copy = [...state];
            let comp = copy.filter((item)=>{ return item.id===datacontain.id});
            if(comp.length>0){                
                comp[0].quantity--;
            }
            return copy;            
        }
        case 'shoes-order':{
            const datacontain = 액션.payload;
            let copy = [...state];
            let comp = copy.filter((item)=>{ return item.id===datacontain.id});
            if(comp.length>0){                
                comp[0].quantity++;
            }
            else{                
                copy.push(datacontain);
            }
            return copy;
        }
        default:
            return state;
    }

    // if( 액션.type==='수량증가' ){
    //     const datacontain = 액션.payload;
    //     let copy = [...state];
    //     copy[0].quan++;
    //     return copy
    // }
    // else if( 액션.type==='수량감소' ){
    //     let copy = [...state];
    //     copy[0].quan--;
    //     return copy
    // }
    // else{
    //     return state
    // }  
}

let store = createStore(combineReducers({reducer,reducer2,reducer3}));

export default store;